import style from '../base';

const Viz = () => (
    <div>
        This is a site that I set up for the communities in the clouds project, a collaboration with Microsoft Research, looking at the role of technology in highrises.  I created this site as a way of engaging residents in the barbican.  The categories and sentiment sections present some simple visualisations of data from Barbican's forum.  Interaction was accomplished using <span class={style.tech}>d3.js</span>.  I've re-created the site in an iframe to show what it looked like:
        <iframe src="/sc/viz/barbican" class={style.frame}></iframe>
    </div>
   
);

export default Viz;


